import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";

import { FormGroup } from "@angular/forms";
import { ConfigModel } from "@soushians/config";
import { Observable } from "rxjs/Observable";
import { ActivatedRoute, Router } from "@angular/router";

@Component({
	selector: "profile-edit",
	template: `<div fxFlex="450px">

  
  <ngs-form-view #form [id]="formId" [card]="true" (accept)="editProfile(form.formGroup)" (cancel)="goback()" ></ngs-form-view>
  <!-- <form [formGroup]="formGroup" (ngSubmit)="editProfile()" fxLayout="column">
    <mat-card>
      <mat-card-content>
      </mat-card-content>
      <mat-card-actions align="end">
        <button type="button" routerLink=".." mat-button color="primary">Ø¨Ø§Ø²Ú¯Ø´Øª</button>
        <button type="submit" mat-raised-button color="primary">ÙÛØ±Ø§ÛØ´</button>
      </mat-card-actions>
    </mat-card>
  </form> -->
</div>`
})
export class ProfileEditComponent implements OnInit {
	@Output() submited = new EventEmitter();
	@Input() userInfo: any;
	@Input() formId: string;
	@Input("roles") roles$: Observable<string[]>;
	@Input() groups: string[];
	constructor(private router: Router) {}

	ngOnInit() {}
	editProfile(form: FormGroup) {
		if (!form.valid) return;
		this.submited.emit(form.value);
	}
	goback() {
		this.router.navigate([ ".." ]);
	}
}
